$red: #EC231A;

.lk {
  .button--order[disabled] {
    opacity: 0.4;
  }
  .lk__downloads {
    opacity: 0.4;
    &.on {
      opacity: 1;
    }
  }
}

#registration .button--red[disabled] {
  opacity:0.4;
}

.comments-rating .rating-symbol {
  font-size:21px;
  margin-left:13px;
  color:#ffad09;
}

.nav-scroll {
  overflow:visible;
  height: auto;
  .nav-tabs {
    position: relative;
    display:inline-block;
    white-space: normal!important;
    margin-bottom:10px;
    .nav-item {
      margin-bottom:10px!important;
    }
  }
}

.text-red {
  color: $red;
}

.text-semibold {
  font-weight:500;
}

@media (max-width: 767px) {
  .nav-tabs--custom .nav-item {
    display: block;
    white-space: nowrap;
    padding-bottom: 15px;
  }
}